
import React from 'react'
import './Configurator.css'

export default function Configurator() {
    function func1(color_chenge) {
        const colorChenge1 = document.documentElement.style.getPropertyValue('--color1');
        document.documentElement.style.setProperty('--color1', color_chenge)

    }
    function func2(color_chenge) {
        const colorChenge2 = document.documentElement.style.getPropertyValue('--color2');
        document.documentElement.style.setProperty('--color2', color_chenge)
    }
    function func3(color_chenge) {
        const colorChenge3 = document.documentElement.style.getPropertyValue('--color3');
        document.documentElement.style.setProperty('--color3', color_chenge)
    }
    // const colorChenge1 = document.documentElement.style.getPropertyValue('--color1');
    // console.log("12121", colorChenge1);
    return (
        <div className="configurator">
            <br></br>
            <br></br>
            <br></br>
            <center>
                <div className="text_chenge" >change the progress color: </div>
                <br></br>
                <div>task in process:<input type="color" onChange={e => func1(e.target.value)}></input></div> ‚Äè
            <div>mission accomplished:<br></br> <input type="color" onChange={e => func2(e.target.value)}></input></div>
                <div>Task not started:<br></br><input type="color" onChange={e => func3(e.target.value)}></input></div>
            </center>
        </div>
    )

}
